@charset "utf-8";
@fs:75px;
.fs(@v){font-size:@v/@fs*1rem;}
body{.fs(24);}
body{font-family:"微软雅黑",tahoma,"Arial";color:#666;-webkit-font-smoothing:antialiased;}
a{text-decoration:none;color:#6c6c6c;}
.tc{text-align:center;}
.db{display:block;}
.bc{margin-left:auto;margin-right:auto;}
.df{display:-webkit-box;display:-webkit-flex;display:flex;}
.fdc{flex-direction:column;}/*主轴切换为纵轴*/
.jcc{justify-content:center;}/*主轴居中对齐*/
.jcsb{justify-content:space-between;}/*主轴两端对齐*/
.jcsa{justify-content:space-around;}/*主轴等分*/
.aic{align-items:center;}/*垂直主轴方向居中*/

.nowrap{overflow:hidden;text-overflow:ellipsis;white-space:nowrap;}
.fl{float:left;display:inline;}
.fr{float:right;display:inline;}
.clearfix:after{content:"";height: 0;display: block;overflow: hidden;visibility: hidden;clear:both;}
.clearfix{*zoom:1;}

/*样式重置 start*/
html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img,textarea,input,th,td,pre,button{margin:0;padding:0}
h1,h2,h3,h4,h5,h6{font-weight:normal;}
ul,ol{list-style: none;}
fieldset,img{border:0 none;vertical-align:top;}
/* ins del strong 常用没有重置样式 */
b,i,address,caption,cite,code,dfn,em,th,var{font-style:normal;font-weight:normal;}
abbr,acronym{border:0;}
/*input会有2px的边框，看情况取消！！！
行内input容易受空格影响，可加浮动解决*//*input{border:0 none;outline:0 none;}*/
input{outline:0 none;}
q:before, q:after{content:'';}
table{border-collapse:collapse;border-spacing:0;}
/*样式重置 end*/


